const Cards = () =>{
    
    const player = usePlayer();
    const dispatch = useDispatch();
    const onCardClick = (index) =>  dispatch(applyCard(index));

    const config = {
        onSwipedUp: () => console.log('swiped up! :'),
        preventDefaultTouchmoveEvent: true,
        trackMouse: true
    };
    
    const swipeCard = ( index, event ) => {
        if (event.dir === 'Up'){
            onCardClick(index)
        }
    }

    if (isMobile()){
        return(
            <div className={styles.handContainer}>
                {
                player.hand.map( (cardData, index) => 
                    <Swipeable key={index} className={styles.swipeContainer} onSwiped={ (event) => swipeCard(index, event) }>
                        <div id={index} className={cardStyle.card}>
                            <Card 
                                cardData={cardData} 
                                combat={true}
                            />
                        </div>
                    </Swipeable>
                    )
                }
            </div>
        ) 

    }else{
        return(
            <div className={styles.handContainer}>
                {
                    player.hand.map( (cardData, index) =>
                        <button key={index} className={cardStyle.cardButton} onClick={ () => onCardClick(index) }>
                            <div className={cardStyle.card}>
                                <Card 
                                    cardData={cardData} 
                                    combat={true}
                                />
                            </div>
                        </button>                
                    )
                }                
            </div>
        )
    }

    
}

const Hand = () => {
    const dispatch = useDispatch()

    return (
        <div className = {styles.activeZone}>
            <div className = {styles.hand}>
                <Cards /> 
            </div>
        </div>
    );
}


export default Hand;

